<template>
    <div>

            <!-- LIST VIEW -->
            <template>
              <div class="vx-row">
                <div class="vx-col w-full lg:w-1/2 mb-base" v-for="item in books" :key="item.attributes.id">
                    <item-list-view :item="item">
                        <template slot="action-buttons">
                            <div
                                v-if="isInCart(item.attributes.id)"
                                class="item-view-secondary-action-btn bg-success p-3 rounded-lg flex flex-grow items-center justify-center text-white cursor-pointer"
                                @click="cartButtonClicked(item)">
                                <feather-icon icon="ShoppingBagIcon" svgClasses="h-4 w-4" />

                                <span class="text-sm font-semibold ml-2">ADICIONADO</span>
                            </div>
                            <div
                                v-else
                                class="item-view-secondary-action-btn bg-primary p-3 rounded-lg flex flex-grow items-center justify-center text-white cursor-pointer"
                                @click="cartButtonClicked(item)">
                                <feather-icon icon="ShoppingBagIcon" svgClasses="h-4 w-4" />
                                <span class="text-sm font-semibold ml-2">ADICIONAR</span>
                            </div>
                        </template>
                    </item-list-view>
                </div>
              </div>
            </template>

            <vs-button
                @click="goToCheckout()"
                class="float-right"
                color="danger"
                type="filled">
                Avançar
            </vs-button>
    </div>

</template>

<script>

export default {
  components: {
    ItemListView: () => import("./components/ItemListView.vue"),
  },
  data() {
    return {
      books: [
        {
          "type": "books",
          "id": "1",
          "attributes": {
            "id": "1",
            "title": "Title Test 1",
            "author": "Author Test 1",
            "publishingHouse": "Publishing House Test 1",
            "isbn": "ISBN Test 1",
            "price": 100,
            "status": 1
          }
        },
        {
          "type": "books",
          "id": "2",
          "attributes": {
            "id": "2",
            "title": "Title Test 2",
            "author": "Author Test 2",
            "publishingHouse": "Publishing House Test 2",
            "isbn": "ISBN Test 2",
            "price": 200,
            "status": 1
          }
        },
      ],
      clickNotClose: true,
    }
  },
  computed: {

    // GRID VIEW
    isInCart() {
      return (itemId) => this.$store.getters['eCommerce/isInCart'](itemId)
    },
    windowWidth() { return this.$store.state.windowWidth }
  },
  watch: {
    windowWidth() {
      this.setSidebarWidth()
    }
  },
  methods: {
    setSidebarWidth() {
      if (this.windowWidth < 992) {
        this.isFilterSidebarActive = this.clickNotClose = false
      } else {
        this.isFilterSidebarActive = this.clickNotClose = true
      }
    },

    // GRID VIEW - ACTIONS
    toggleFilterSidebar() {
      if (this.clickNotClose) return
      this.isFilterSidebarActive = !this.isFilterSidebarActive
    },
    additemInCart(item) {
      this.$store.dispatch('eCommerce/additemInCart', item)
    },
    cartButtonClicked(item) {
      this.$store.dispatch('eCommerce/toggleItemInCart', item)
      // this.isInCart(item.attributes.id) ? this.$router.push('/app/books/checkout').catch(() => {}) : this.additemInCart(item)
    },
    goToCheckout() {
      this.$router.push('/app/books/checkout')
    }
  },
  created() {
    this.setSidebarWidth()
  }
}

</script>


